//===========Label===========//

$label-default-font-size: 12px;
$label-default-height: 24px;
$label-default-min-width: 0;
$label-default-padding: 0 12px;
$label-default-border-radius: 12px;

$label-sm-font-size: 10px;
$label-sm-height: 20px;
$label-sm-min-width: 0;
$label-sm-padding: 0 7px;
$label-sm-border-radius: 3px;

$label-md-font-size: 11px;
$label-md-height: 22px;
$label-md-min-width: 0;
$label-md-padding: 0 8px;
$label-md-border-radius: 3px;

$label-lg-font-size: 12px;
$label-lg-height: 24px;
$label-lg-min-width: 0;
$label-lg-padding: 0 9px;
$label-lg-border-radius: 3px;

label {
  margin-bottom: 0;
}
.ulabel {
  &-status {
    display: inline-block;
    white-space: nowrap;
    vertical-align: middle;
  }
  &-size {
    &-default {
      border-radius: $label-default-border-radius;
      height: $label-default-height;
      line-height: $label-default-height;
      min-width: $label-default-min-width;
      font-size: $label-default-font-size;
      padding: $label-default-padding;
    }
    &-sm {
      border-radius: $label-sm-border-radius;
      height: $label-sm-height;
      line-height: $label-sm-height;
      min-width: $label-sm-min-width;
      font-size: $label-sm-font-size;
      padding: $label-sm-padding;
    }
    &-md {
      border-radius: $label-md-border-radius;
      height: $label-md-height;
      line-height: $label-md-height;
      min-width: $label-md-min-width;
      font-size: $label-md-font-size;
      padding: $label-md-padding;
    }
    &-lg {
      border-radius: $label-lg-border-radius;
      height: $label-lg-height;
      line-height: $label-lg-height;
      min-width: $label-lg-min-width;
      font-size: $label-lg-font-size;
      padding: $label-lg-padding;
    }
  }

  &-round {
    &.label-size-default {
      border-radius: $label-default-height/2;
    }
    &.label-size-sm {
      border-radius: $label-sm-height/2;
    }
    &.label-size-md {
      border-radius: $label-md-height/2;
    }
    &.label-size-lg {
      border-radius: $label-lg-height/2;
    }
  }
  &-full {
    width: 100%;
    display: block;
  }
  &-sub {
    font-size: 12px;
    padding-top: 5px;
    display: block;
  }
}

.tag {
  display: flex;
  align-items: center;
  border-radius: 12px;
  height: 24px;
  padding: 0 8px;
  justify-content: center;
  border: 1px solid $color-secondary;
  &.ubg-sub-primary {
    border: 1px solid $color-hover-sub-primary;
  }
  &-square {
    border-radius: $base-border-radius;
  }
  &-ic {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    line-height: 1;
    &.order-2 {
      margin-left: 4px;
      margin-right: 0;
    }
  }
}
