$tabsLine: 'default';

.nav {
  &-style {
    &-#{$tabsLine} {
      .nav-link {
        padding: 16px;
        font-size: $fz-p1;
        font-weight: 500;
        border-bottom: 2px solid transparent;
        color: $color-default !important;
        &.active {
          border-bottom: 2px solid $color-primary;
          color: $color-primary !important
        }
      }
    }
  }
  &-equal {
    > * {
      flex: 1;
      text-align: center;
    }
  }
}
