//gtc-text-scss
$font-family: Muli,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;

$map-h-font: (
	"d1": (
		"768": (
			"font-size": "28px"
		),
		"font-size": "40px",
		"line-height": "1.2",
		"font-weight": "600"
	),
	"d2": (
		"font-size": "22px",
		"line-height": "1.4"
	),
	"h1": (
		"font-size": "28px",
		"line-height": "1.4",
		"font-weight": "500"
	),
	"h2": (
		"font-size": "22px",
		"line-height": "1.4",
		"font-weight": "500"
	),
	"h3": (
		"font-size": "16px",
		"line-height": "1.5",
		"font-weight": "500"
	),
	"h4": (
		"font-size": "14px",
		"line-height": "1.4",
		"font-weight": "500"
	),
	"h5": (
		"font-size": "12px",
		"line-height": "1.3",
		"font-weight": "600"
	),
	"p1": (
		"font-size": "16px",
		"line-height": "1.5"
	),
	"p2": (
		"font-size": "14px",
		"line-height": "1.4"
	),
	"p3": (
		"font-size": "12px",
		"line-height": "1.4"
	),
	"default": (
		"font-size": "14px",
		"line-height": "1.4"
	)
);
$fz-d1: 40px;
$fz-d1-line-height: 1.2;
$fz-d2: 22px;
$fz-d2-line-height: 1.4;
$fz-h1: 28px;
$fz-h1-line-height: 1.4;
$fz-h2: 22px;
$fz-h2-line-height: 1.4;
$fz-h3: 16px;
$fz-h3-line-height: 1.5;
$fz-h4: 14px;
$fz-h4-line-height: 1.4;
$fz-h5: 12px;
$fz-h5-line-height: 1.3;
$fz-p1: 16px;
$fz-p1-line-height: 1.5;
$fz-p2: 14px;
$fz-p2-line-height: 1.4;
$fz-p3: 12px;
$fz-p3-line-height: 1.4;
$fz-default: 14px;
$fz-default-line-height: 1.4;

//end-gtc-text-scss

