$map-modal-size: (
  default: 570px,
  alert-default: 320px,
  ticket: 540px,
  xs: 373px,
  sm: 570px,
  smd: 720px,
  md: 1000px,
  lg: 1200px,
) !default;

$modal-space: 32px !default;
$modal-backdrop-bgcolor: $color-black !default;
$modal-backdrop-bgcolor-op: 0.5 !default;

.bd-example-modal {
  padding: 1px;
  background-color: rgba($modal-backdrop-bgcolor, $modal-backdrop-bgcolor-op);
  border-radius: 15px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  .modal,
  .toast {
    position: relative;
    top: auto;
    right: auto;
    bottom: auto;
    left: auto;
    z-index: 1;
    display: block;
    opacity: 1 !important;
    transform: none !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  > *:not(.modal) {
    margin: 30px auto;
    pointer-events: all;
  }
  .modal-dialog {
    left: auto;
    margin-right: 30px;
    margin-left: 30px;
    transform: none !important;
    max-width: 100%;
  }
}

/* modal */
.col-btn-close {
  min-width: 60px;
}
.close-modal-btn {
  height: 40px;
  line-height: 60px;
  display: block;
  position: absolute;
  right: 10px;
  top: -20px;
}
.alert-modal-ic ~ .modal-title {
  margin-top: 0;
}

.modal {
  z-index: 3000;
  -webkit-overflow-scrolling: touch;
  &-backdrop {
    background-color: $modal-backdrop-bgcolor;
    &.show {
      opacity: $modal-backdrop-bgcolor-op;
    }
  }
  &-size {
    @include modifiers($map-modal-size, "width");
  }
  &-wrap {
    width: 100%;
  }
  &.title {
    margin-top: $modal-space/2;
  }
  &-content {
    border: 0;
    border-radius: 10px;
    box-shadow: 5px 10px 20px 0 rgba($color-default, 0.03);
  }
  &-dialog-centered {
    .modal-content {
      overflow: hidden;
    }
  }
  &-header {
    padding: $modal-space $modal-space/2 5px $modal-space/2;
    border: 0;
  }
  &-body {
    border: 0;
    text-align: inherit;
    padding: $modal-space/2 $modal-space/2;
  }
  &-footer {
    padding: 10px $modal-space/2;
    border: 0;
    display: block;
    > * {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
  &-close {
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    min-width: 40px;
    img {
      width: 16px;
    }
  }
  &-alert {
    .modal-header {
      padding-bottom: 0;
    }
    .modal-body {
      padding-top: $modal-space/2;
      padding-bottom: $modal-space/2;
    }
  }
  .open > [data-content] {
    max-height: none !important;
  }
  &-dialog {
    max-width: calc(100vw - 15px);
  }
  &-dialog-scrollable {
    .modal-header,
    .modal-footer {
      position: relative;
      z-index: 1;
      &::after {
        position: absolute;
        content: "";
        left: 0;
        width: 100%;
        height: $modal-space/3;
        pointer-events: none;
        // background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(rgba(255, 255, 255, 0.03)));
        // background: linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
      }
    }
    .modal-header {
      &::after {
        bottom: -$modal-space/4;
      }
    }
    .modal-footer {
      &::after {
        top: -$modal-space/4;
        transform: rotate(180deg);
      }
    }
  }
}

.modal {
  &-title {
    padding-right: 20px;
  }
  &-header {
    padding: $modal-space;
    padding-bottom: 12px;
  }
  &-body {
    padding: 12px $modal-space;
  }
  &-footer {
    padding: 12px $modal-space $modal-space $modal-space;
  }
  &-extend-section {
    padding: 16px 32px;
    border-bottom: 1px solid $color-porcelain-dark;
  }
  @include media(">=768px") {
    &-open {
      overflow-y: scroll !important;
    }
  }
  @include media("<768px") {
  }
  @include media("<=575px") {
    .modal-dialog {
      margin: 0.5rem auto;
    }
  }
}
.modal-type-block {
  overflow: hidden !important;
  .modal {
    &-header {
      padding-bottom: $modal-space;
      border-bottom: 1px solid $color-porcelain-light;
    }
    &-body {
      padding: $modal-space;
      background-color: $color-porcelain-light;
    }
    &-footer {
      border-top: 1px solid $color-porcelain-light;
      padding: $modal-space;
    }
  }
}

.modal.fade:not(.modal-static) .modal-dialog {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: none;
}

.modal-close-btn {
  position: absolute;
  top: 4px;
  right: 4px;
}

@media (min-width: 768px) {
  body {
    padding-right: 0 !important;
  }
}

.modal-size-xs {
  .modal {
    &-body,
    &-header,
    &-footer {
      padding: 24px;
    }
  }
}

/*end modal*/

/* uinline-modal */

.uinline-modal--inner {
  pointer-events: all;
  transition: 0.2s all ease-in-out;
}
.uinline-modal {
  height: 100%;
}
.uinline-modal .uinline-modal-wrap {
  height: 100%;
  margin: auto;
}
.uinline-modal .uinline-modal-wrap:not(.container) {
  max-width: 100%;
}
.uinline-modal .uinline-modal-wrap.container {
  padding-left: 0;
  padding-right: 0;
}
.uinline-modal--inner {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.uinline-modal.show {
  opacity: 1 !important;
  visibility: visible !important;
  pointer-events: all !important;
  background-color: rgba($color-default, 0.5);
}

.uinline-modal-backdrop {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  opacity: 0;
  pointer-events: none;
  & ~ * {
    z-index: 1;
    position: relative;
  }
}
.uinline-modal-backdrop.show {
  opacity: 1;
  pointer-events: all;
}

@media (max-width: 1199px) {
  .uinline-modal.d-xl-block {
    z-index: 900;
    left: 0;
    right: 0;
    margin: auto;
    position: fixed;
    bottom: 0;
    max-width: 100%;
    overflow: auto;
    display: block !important;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: 0.2s all ease-in-out;
    .uinline-modal--inner {
      box-shadow: 0 0 4px rgba($color-default, 0.3);
      background-color: $color-invert;
      border-radius: 6px;
    }
  }
}

@media (min-width: 1200px) {
  .uinline-modal.d-xl-block {
    background-color: transparent !important;
    .uinline-modal--inner {
      margin: 0;
    }
    .uinline-modal-wrap,
    .modal-body,
    .modal-footer {
      padding: 0;
    }
    .modal-header {
      padding-top: 0;
      padding-left: 0;
      padding-right: 0;
    }
    .uinline-modal--inner {
    }
  }
}

@media (max-width: 600px) {
  .uinline-modal--inner {
    pointer-events: all;
    margin-left: 0;
    margin-right: 0;
  }
}

@media (min-width: 576px) {
  .uinline-modal {
  }
  .uinline-modal .uinline-modal-wrap {
    height: auto;
    min-height: 100%;
    padding-top: 12px;
    padding-bottom: 12px;
  }
  .modal-type-default .modal-dialog-scrollable {
    max-height: none;
  }
  .modal-type-default .modal-dialog-scrollable .modal-body {
    overflow: inherit;
  }
}

@media (max-width: 575px) {
  .uinline-modal {
    overflow: hidden !important;
  }
  .uinline-modal .uinline-modal-wrap {
    padding: 0;
  }
  .uinline-modal--inner {
    height: 100%;
    width: 100%;
    max-height: none;
    border-radius: 0;
    transform: translateY(100%);
  }
  .uinline-modal.show .uinline-modal--inner {
    transform: translateY(0);
  }
  .uinline-modal.d-xl-block .uinline-modal--inner {
    border-radius: 0;
  }
  .modal-type-default .modal-footer,
  .modal-type-default .modal-header {
    box-shadow: 0 0 8px rgba($color-default, 0.16);
  }
  .modal-close-btn {
    top: 0;
    bottom: 0;
    margin: auto;
    display: flex;
    align-items: center;
  }
  .modal-type-block,
  .modal-type-default {
    .modal-header,
    .modal-footer,
    .modal-extend-section,
    .modal-body {
      padding: 16px 16px;
      .h2 {
        font-size: $fz-h3;
      }
    }
  }
  .modal-footer,
  .dropdown-footer,
  .form-btn-group {
    & > .row {
      flex-direction: row-reverse;
      margin-bottom: -8px;
      > * {
        min-width: 50%;
        flex-grow: 1;
        margin-bottom: 8px;
        &:nth-last-of-type(1) {
          order: 1;
        }
        &:nth-last-of-type(2) {
          order: 2;
        }
        &:nth-last-of-type(3) {
          order: 3;
        }
      }
    }
  }
}

/* end uinline-modal */

/* toast */
.toast {
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 1041;
  border-radius: 8px;
  background-color: $color-invert;
  backdrop-filter: blur(10px);
  color: $color-default;
  border: 0;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.15);
  min-width: 350px;
  flex-basis: 350px;
  pointer-events: none;
}
.toast.showing,
.toast.show {
  pointer-events: all;
}
.toast-body {
  padding: 10px 15px;
  word-break: break-word;
  width: 100%;
}

@media (max-width: 767px) {
  .toast .flex-grow-1 {
    flex: 1 0 0;
  }
  .addDB {
    padding: 0;
  }
  .toast {
    top: auto;
    left: 10px;
    right: 10px;
    margin: auto;
    bottom: 40px;
    flex-basis: 100%;
    min-width: 0;
  }
}
/* end toast */

// modal ticket
.modal-ticket {
  .modal {
    &-body {
      padding: 0;
    }
  }
  .modal-content {
    background-color: transparent;
    border-radius: 6px;
  }
}
// end modal ticket

.modal-type-alert {
  .modal-title {
    padding-right: 0;
  }
  .modal-body {
    color: $color-sub-default;
    padding: 12px 24px;
  }
  .modal-header {
    padding: 24px;
    padding-bottom: 12px;
  }
  .modal-footer {
    padding: 12px 24px 24px 24px;
  }
}

.modal-after-header {
  padding-left: 32px;
  padding-right: 32px;
  @include media("<=575px") {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.modal-full-height {
  .modal-dialog,
  .tab-content,
  .tab-pane {
    height: 100%;
  }
  
}

.modal-body-no-padding {
  .modal-body {
    padding-left: 0;
    padding-right: 0;
  }
}