$map-dropdown-size: (
  default: 200px,
  sm: 244px,
  md: 280px,
) !default;

.bd-example-modal {
  > * {
    max-width: 100%;
  }
  .dropdown-menu {
    max-width: 100%;
    position: relative;
    display: block;
    float: none;
  }
}

.dropdown {
  &-body{
    overflow-y: auto;
    flex: 1 1 auto;
  }
  &-footer {
    padding: 16px;
    border: 1px solid $color-secondary;
    border-radius: 0 0 3px 3px;
    .form-btn-group{
      margin: 0;
    }
  }
  &-it {
    display: block;
    border-radius: 8px;
    padding: 4px 8px;
    cursor: pointer;
    min-height: 36px;
  }
  &-size {
    @include modifiers($map-dropdown-size, "width");
  }
  &-menu {
    border-radius: 3px;
    border: 0;
    box-shadow: 0 4px 8px 0 rgba($color-default, 0.16);
    margin-top: 5px;
    padding: 0;
    font-size: inherit;
    z-index: 1040;
    > li:not(:last-child) {
      margin-bottom: 4px;
      border-bottom: 1px solid $border-base-color;
    }
    .seperatetor {
      height: 1px;
      margin: 10px 0 !important;
    }
    .row {
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }
  }
}

.dropdown-menu.show {
  display: block !important;
}
.dropdown-mobile-header--only {
  display: none;
}

.dropdown-mobile-title {
  display: none;
}
.dropdown-uheader-content:only-child {
  padding: 16px;
}
@media (max-width: 480px) {
  .dropdown-menu.dropdown-mobile.width-auto {
    width: 100% !important;
    max-width: none !important;
  }
  .dropdown-menu.dropdown-mobile {
    position: fixed !important;
    z-index: 4000 !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    max-height: 80vh;
    top: auto !important;
    transform: translateY(100%) !important;
    width: 100% !important;
    transition: 0.3s all ease-in-out;
    margin: auto !important;
    display: flex !important;
    flex-direction: column;
    visibility: hidden;
  }

  .dropdown-menu.dropdown-mobile.show {
    transform: translateY(0) !important;
    visibility: visible;
  }
  .dropdown-menu.dropdown-mobile.dropdown-mobile--full {
    top: 0 !important;
    max-height: none !important;
    border-radius: 0;
  }
  .dropdown-mobile-title {
    display: block;
  }
  .dropdown-mobile-title {
    padding: 4px 16px;
    border-bottom: 1px solid $border-base-color;
    display: block;
    .dropdown-close.ubg-transparent {
      position: relative;
      right: -12px;
    }
  }
}

.dropdown-tooltip-fake.show {
  &::before {
    left: 0;
    right: 0;
    margin: auto;
    display: block;
    width: 0.8rem;
    height: 0.4rem;
    position: absolute;
    content: "";
    border-color: transparent;
    border-style: solid;
    border-top-color: $color-invert;
    border-bottom-color: $color-invert;
    border-width: 0 0.4rem 0.4rem;
    bottom: -0.4rem;
    box-shadow: 0 4px 16px rgba($color-default, 0.2);
  }
}

.dropdown-inner-tooltip-fake {
  box-shadow: 0 4px 16px rgba($color-default, 0.2);
}
